import React, { Props } from "react";
import { TopicIconComponent } from "./topicIconComponent";
import { TopicTitleComponent } from "./topicTitleComponent";
import { TopicDescriptionComponent } from "./topicDescriptionComponent";
import "./topicComponent.css";
import {
  Anchor,
  Box,
  Button,
  Card,
  CardBody,
  CardFooter,
  Collapsible,
  Heading,
  Grommet,
  Image,
  Paragraph,
} from 'grommet';

export type TopicProps = {
  id: any;
  imagelink: any;
  title: string;
  description: string;
  className: string;
};

const theme = {
  global: {
    font: {
      family: 'Prosto One'
    },
  },
  card: {
    container: {
      background: '#FFFFFF10',
      elevation: 'none',
    },
    footer: {
      pad: { horizontal: 'medium', vertical: 'small' },
    },
  },
  heading: {
    color: '#FFFFFF'
  }
};

export const TopicComponent: (props: TopicProps) => JSX.Element = (
  props: TopicProps
) => {
  return (
    <Grommet theme={theme}>
      <Card elevation="large" width="medium">
        <CardBody height="small">
          <Image
            fit="cover"
            src={props.imagelink}
          />
        </CardBody>
        <Box pad={{ horizontal: 'medium' }} responsive={false}>
          <Heading level="4" margin={{ vertical: 'medium' }}>
            {props.title}
          </Heading>
          <Paragraph margin={{ top: 'none' }}>
            {props.description}
          </Paragraph>
        </Box>
        <CardFooter>
          <Box direction="row" align="center" gap="small">
            <Anchor
              href="https://www.collinsdictionary.com/us/dictionary/english/bridge"
              label="More"
            />
          </Box>
        </CardFooter>
      </Card>
    </Grommet>
    // <>
    /*{ <div className={props.className + " topicComponent"} id={props.id}>
      <div className="topicIconComponent">
        <TopicIconComponent imagelink={props.imagelink}/>
      </div>
      <div className="topicTitleComponent">
        <TopicTitleComponent title={props.title}/>
      </div>
      <div className="topicDescriptionComponent"> 
        <TopicDescriptionComponent description={props.description}/>
      </div>
      <Button className="topicComponentButton">MORE</Button>
    </div> }*/
    // {/* </> */ }
  );
};
